<template>
    <div class="account-main wrap fColor1">
        <div class="title flex">
            <span>{{$t('jc.pass')}}</span>
        </div>
        <div class="main-content mt20">
            <ul class="plr20 ptb15 l_box">
                <li v-for="item in list" class="flex alcenter plr15 ptb15 mt10 radius4 bgGray">
                    <div class="flex1">
                        <span class="blue2 ft12">{{$t('asset.currency')}}</span>
                        <p class="mt10">{{item.currency_name}}</p>
                    </div>
                    <div class="flex1 tc">
                        <span class="blue2 ft12">{{$t('jc.activeMoney')}}</span>
                        <p class="mt10">{{item.active_number}}</p>
                    </div>
                    <div class="flex1 tr btn-box tc">
                        <p class="act radius4 plr10 btnbg fr tc pointer bgBlue" v-if="item.status==0" @click="active(item.id)">{{$t('jc.active')}}</p>
                        <p class="act  radius4 plr10 tc fr" v-else>{{$t('jc.actived')}}</p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
           list:[]
        }
    },
    filters: {
        toFixeds: function(value) {
            value = Number(value);
            return value.toFixed(2);
        }
    },
    computed: {
       
    },
    created(){
        this.getlist();
    },
    mounted(){
        
    },
    methods:{
        getlist(){
            var that = this;
            this.$http({
                url: '/api/pass/list',
                method:'GET',
                headers: {'Authorization':  localStorage.getItem('token')},
            }).then(res=>{
                if(res.data.type=="ok"){
                    that.list = res.data.message;
                }
            }).catch(error=>{
               
            })
        },
        active(id){
            let that = this;
            let loa = layer.load();
            this.$http({
                url: '/api/pass/active',
                method:'POST',
                data:{
                    id:id
                },
                headers: {'Authorization':  localStorage.getItem('token')},
            }).then(res=>{
                layer.close(loa);
                layer.msg(res.data.message);
                that.getlist();
            }).catch(error=>{
               
            })
        }
    }
}
</script>
<style lang='scss'>
    .ptb15{
        padding-top: 15px;
        padding-bottom: 15px;
        
    }
    .plr15{
        padding-left: 15px;
        padding-right: 15px;
    }
    .l_box li{
        background: #25304b;
    }
    .exbox{
        padding-top: 60px;
    }
    .btn-box p{
        width: 80px;
        padding: 3px 0;
    }
    .bgBlue{
        background: #357ce1;
    }
    .btn-btn{
        line-height: 38px;
        padding: 0 30px;
        background: #5697f4;
        margin-left: 20px;
    }
    .number-input{
        color: #c7cce6;
        display: block;
        width: 100%;
        line-height: 38px;
        padding: 0 20px;
        border: 1px solid #4e5b85;
        background: none;
        margin-left: 10px;
    }
    .el-input__inner {
        border: 1px solid #4e5b85;
        background: #1a243b;
        color: #fff;
    }
    .layui-layer{
    background:#171c2b;
    }
    .layui-layer-title {
    border-bottom: 1px solid #2e3753;
    color: #fff;
    background-color: #171c2b;
    }
    .layui-layer-prompt .layui-layer-input {
    border: 1px solid #555869;
    color: #fff;
    background: none;
    }
    .layui-layer-dialog .layui-layer-content{
    color: #fff;
    }
    .layui-layer-loading{
    background-color: transparent;
    }
   .account-main {
        background: #273041;
        .title {
            width: 100%;
            line-height: 60px;
            background: #1a243b;
            border-radius: 4px;
            padding-left: 20px;
        }
        .main-content {
            background: #1a243b;
            min-height: 700px;
            border-radius: 2px;
            .inbox{padding-top: 50px}
            .numbers{
                background: transparent;
                border:1px solid #202731;
                height: 28px;
                margin-left: 10px;
                padding-left: 10px;
                width:140px;
                color: #ffffff;
                font-size: 12px;
            }
            .mt60{margin-top: 60px;}
            .submit{
                background: #5697f4;
                height: 32px;
                margin-left: 10px;
                width: 120px;
                color: #ffffff;
                cursor: pointer;
            }
        }
        .topboxs{
            width: 88%;
            margin: 0 auto;
            li{
                flex: 1;
                height: 75px;
                line-height: 32px;
                border: 1px solid #202731;
                p{
                    padding-bottom: 5px;
                    font-size: 12px;

                }
                span{
                    color: #87a2cd;
                }
                .red{color: #de5151}
            }
        }
        .record{background-color: #1a2330;padding: 10px 20px}
        .ulbox{max-height: 300px;overflow-y: scroll;padding-bottom: 20px;}
        .p20{padding: 20px;}
        .plr20{padding-left: 20px;padding-right: 20px}
        .p10{padding: 10px;}
        .libg{background: #1a2330;padding: 10px;border-radius: 4px;}
        .red{color: #ce3939}
   }
</style>



